
Template.__checkName("SingleQuestionnaireNav");
Template["SingleQuestionnaireNav"] = new Template("Template.SingleQuestionnaireNav", (function() {
  var view = this;
  return HTML.NAV({
    "class": "questionnaire-nav"
  }, "\n    ", HTML.LI("\n      ", HTML.A({
    href: function() {
      return [ "/questionnaire/", Spacebars.mustache(Spacebars.dot(view.lookup("questionnaire"), "_id")) ];
    }
  }, "Overview"), "\n    "), "\n    ", HTML.LI("\n      ", HTML.A({
    href: function() {
      return [ "/questionnaire/", Spacebars.mustache(Spacebars.dot(view.lookup("questionnaire"), "_id")), "/sensor" ];
    }
  }, "Sensors"), "\n    "), "\n    ", HTML.LI("\n      ", HTML.A({
    href: function() {
      return [ "/questionnaire/", Spacebars.mustache(Spacebars.dot(view.lookup("questionnaire"), "_id")), "/configuration" ];
    }
  }, "Configuation"), "\n    "), "\n    ", HTML.LI("\n      ", HTML.A({
    href: function() {
      return [ "/questionnaire/", Spacebars.mustache(Spacebars.dot(view.lookup("questionnaire"), "_id")), "/export" ];
    }
  }, "Export"), "\n    "), HTML.Raw('\n    <li>\n      <a href="/questionnaire-overview">Return to Overview</a>\n    </li>\n  '));
}));
