
Template.__checkName("Questionnaires");
Template["Questionnaires"] = new Template("Template.Questionnaires", (function() {
  var view = this;
  return [ HTML.SECTION({
    "class": "questionnaires"
  }, "\n    ", Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n      ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("questionnaires"));
    }, function() {
      return [ "\n        ", Spacebars.include(view.lookupTemplate("SingleQuestionnaire")), "\n      " ];
    }), "\n    " ];
  }, function() {
    return [ "\n      ", HTML.P("Loading"), "\n    " ];
  }), "\n  "), "\n  ", Spacebars.include(view.lookupTemplate("NewQuestionnaire")) ];
}));
