
Template.__checkName("QuestionnaireSingleConfig");
Template["QuestionnaireSingleConfig"] = new Template("Template.QuestionnaireSingleConfig", (function() {
  var view = this;
  return [ HTML.H1(Blaze.View("lookup:questionnaire.name", function() {
    return Spacebars.mustache(Spacebars.dot(view.lookup("questionnaire"), "name"));
  })), "\n\n  ", Spacebars.include(view.lookupTemplate("SingleQuestionnaireNav")), "\n\n  ", HTML.DIV({
    "class": "new-recipe-container"
  }, "\n    ", HTML.Raw("<h2>Configuration</h2>"), "\n\n    ", HTML.Raw("<h3>Questionnaire start and end date</h3>"), "\n    ", HTML.Raw('<!-- {{> quickForm collection="Questionnaires" id="updateQuestionnaireId" type="update" doc=questionnaire class="new-recipe-form"}} -->'), "\n\n    ", Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Questionnaires"),
      id: Spacebars.call("updateQuestionnaireId"),
      type: Spacebars.call("update"),
      doc: Spacebars.call(view.lookup("questionnaire"))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("autoForm"), function() {
      return [ "\n      ", Blaze._TemplateWith(function() {
        return {
          name: Spacebars.call("configuration")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("afQuickField"));
      }), "\n\n      ", HTML.DIV("\n        ", HTML.BUTTON({
        type: "submit"
      }, "Submit"), "\n      "), "\n\n    " ];
    });
  }), "\n\n  ") ];
}));
