
Template.__checkName("SingleQuestionnaire");
Template["SingleQuestionnaire"] = new Template("Template.SingleQuestionnaire", (function() {
  var view = this;
  return HTML.ARTICLE({
    "class": "recipe"
  }, "\n    ", HTML.H3(Blaze.View("lookup:name", function() {
    return Spacebars.mustache(view.lookup("name"));
  })), "\n    ", HTML.P(Blaze.View("lookup:description", function() {
    return Spacebars.mustache(view.lookup("description"));
  })), "\n    ", HTML.P({
    "class": "meta"
  }, "Created ", Blaze.View("lookup:formatDate", function() {
    return Spacebars.mustache(view.lookup("formatDate"), view.lookup("createdAt"));
  }), "."), "\n\n    ", Blaze.View("lookup:nrQuestionnaires", function() {
    return Spacebars.mustache(view.lookup("nrQuestionnaires"));
  }), HTML.Raw('\n    <!-- <p>\n      {{#each questions}}\n        <span class="ingredient">{{name}} - {{type}}</span>\n      {{/each}}\n    </p> -->\n    '), HTML.A({
    href: function() {
      return [ "/questionnaire/", Spacebars.mustache(view.lookup("_id")) ];
    }
  }, "View Details"), HTML.Raw('\n\n    <i class="fa fa-trash"></i>\n  '));
}));
