
Template.__checkName("QuestionnaireSingleSensor");
Template["QuestionnaireSingleSensor"] = new Template("Template.QuestionnaireSingleSensor", (function() {
  var view = this;
  return [ HTML.H1(Blaze.View("lookup:questionnaire.name", function() {
    return Spacebars.mustache(Spacebars.dot(view.lookup("questionnaire"), "name"));
  })), "\n\n  ", Spacebars.include(view.lookupTemplate("SingleQuestionnaireNav")), "\n\n  ", HTML.DIV({
    "class": "new-recipe-container"
  }, "\n    ", HTML.Raw("<h2>Sensor magic</h2>"), "\n\n    ", Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Questionnaires"),
      type: Spacebars.call("update"),
      doc: Spacebars.call(view.lookup(".")),
      id: Spacebars.call("FieldValueIsForm")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("autoForm"), function() {
      return [ "\n\n      ", Blaze._TemplateWith(function() {
        return {
          name: Spacebars.call("sensors")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("afEachArrayItem"), function() {
          return [ "\n          ", Blaze._TemplateWith(function() {
            return {
              name: Spacebars.call(Spacebars.dot(view.lookup("."), "current", "sensorType"))
            };
          }, function() {
            return Spacebars.include(view.lookupTemplate("afFieldInput"));
          }), "\n          ", Blaze._TemplateWith(function() {
            return {
              name: Spacebars.call(Spacebars.dot(view.lookup("."), "current", "sensorActive"))
            };
          }, function() {
            return Spacebars.include(view.lookupTemplate("afFieldInput"));
          }), "\n      " ];
        });
      }), "\n\n", HTML.Comment('\n\n      {{> afQuickField name="GUCCI.$.sensorActive" noselect=true }}\n\n      {{> afQuickField name="CHANEL" noselect=true }}\n\n      {{> afQuickField name="GUCCI" noselect=true }}\n\n      {{#each sensors}}\n\n        <h3>{{this.type}}</h3>\n\n\n\n        {{#if afFieldValueIs name="sensorType" value="GUCCI"}}\n           {{> afQuickField name="shotsTaken"}}\n        {{/if}}\n\n\n        {{> afQuickField name="sensors.$.sensorActive" noselect=true }}\n\n        {{#if afFieldValueIs type="CHANEL" value=true}}\n          {{> afQuickField name="sensors.$.frequency" value=this.frequency}}\n        {{/if}}\n      {{/each}}\n\n      {{#if afFieldValueIs name="sensors.$.sensorType" value="CHANEL"}}\n        {{> afQuickField name="frequency"}}\n      {{/if}}\n'), "\n      ", HTML.DIV("\n        ", HTML.BUTTON({
        type: "submit"
      }, "Submit"), "\n      "), "\n    " ];
    });
  }), "\n  ") ];
}));
