
Template.__checkName("NewQuestionnaire");
Template["NewQuestionnaire"] = new Template("Template.NewQuestionnaire", (function() {
  var view = this;
  return HTML.DIV({
    "class": "recipe"
  }, HTML.Raw("\n    <h3>New questionnaire</h3>\n\n    "), Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Questionnaires"),
      id: Spacebars.call("updateQuestionnaireId"),
      type: Spacebars.call("insert"),
      "class": Spacebars.call("new-recipe-form")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("autoForm"), function() {
      return [ "\n\n      ", Blaze._TemplateWith(function() {
        return {
          name: Spacebars.call("name")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("afQuickField"));
      }), "\n\n      ", Blaze._TemplateWith(function() {
        return {
          name: Spacebars.call("description")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("afQuickField"));
      }), "\n\n      ", Blaze._TemplateWith(function() {
        return {
          name: Spacebars.call("author")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("afQuickField"));
      }), "\n\n      ", Blaze._TemplateWith(function() {
        return {
          name: Spacebars.call("createdAt")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("afQuickField"));
      }), "\n\n      ", HTML.DIV("\n        ", HTML.BUTTON({
        type: "submit"
      }, "Add Questionnaire"), "\n      "), "\n    " ];
    });
  }), HTML.Raw('\n\n\n    <!-- {{> quickForm collection="Questionnaires" id="insertRecipeForm" type="insert" class="new-recipe-form"}} -->\n  '));
}));
