
Template.__checkName("QuestionnaireSingleExport");
Template["QuestionnaireSingleExport"] = new Template("Template.QuestionnaireSingleExport", (function() {
  var view = this;
  return [ HTML.H1(Blaze.View("lookup:questionnaire.name", function() {
    return Spacebars.mustache(Spacebars.dot(view.lookup("questionnaire"), "name"));
  })), "\n\n  ", Spacebars.include(view.lookupTemplate("SingleQuestionnaireNav")), HTML.Raw('\n\n  <div>\n    <h2>Export Questionnaire</h2>\n\n    <p>Test questionnaire in AWARE.</p>\n\n    <button>Export to AWARE</button>\n\n    <div id="qrcode"></div>\n\n  </div>') ];
}));
